import React, { useState } from 'react';
import PropTypes from 'prop-types';

import { Panel,
	 	PanelHeader,
	  	Card,
	 	Button,
	  	Group,
	  	Div,
	  	Title,
	  	TabsItem,
	 	Tabs,
	 	CardGrid  } from '@vkontakte/vkui';

import '../css/Guides.css';
import { ReactComponent as GoButton } from '../img/go.svg';
import { ReactComponent as BottleOn } from '../img/bottle-on.svg';
import { ReactComponent as BottleOff } from '../img/bottle-off.svg';
import { ReactComponent as Tetra } from '../img/tetra.svg';
import { 
	Icon24Cards2Outline,
	Icon28TshirtOutline,
	Icon28AppleOutline,
	Icon28GridLayoutOutline,
	Icon28LightbulbOutline,
	Icon28CubeBoxOutline,
	Icon28MotorcycleOutline,
	Icon28WasherOutline,
	Icon28WarningTriangleOutline } from '@vkontakte/icons';
	

const Guides = ({ id, go, fetchedUser, restateTabState, getTabState }) => {





	return(
		<Panel id={id}>
	
		<Tabs className='tab-div-g'>
            <TabsItem
                onClick={go}
				data-to="home">
                Пункты
            </TabsItem>
            <TabsItem
				data-to="guides"
                selected={true}>
                Обучение
            </TabsItem>
		</Tabs>
				
			<Div> <Title> С чего начнем?</Title></Div>
	
			<div>
			<CardGrid size="l">
			<Card className="card-g" mode="shadow">
				<div style={{ height: 60}}>
				<Icon24Cards2Outline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Бумага </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>


				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<BottleOff className='icon-g' width={48} height={48} />
				<Title className='titles-g'> Пластик </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28TshirtOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Одежда </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28AppleOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Пищевые отходы </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28GridLayoutOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Стекло </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>
				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28LightbulbOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Лампочки </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28CubeBoxOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Упаковки </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28MotorcycleOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Металл </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28WasherOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Бытовые отходы </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28WarningTriangleOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Опасные отходы </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>
				
				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Tetra className='icon-g' width={48} height={48} />
				<Title className='titles-g'> Тетра Пак </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Icon28LightbulbOutline className='icon-g' fill="#99A2AD" width={48} height={48} />
				<Title className='titles-g'> Батарейки </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>


				<Card className="card-g"  mode="shadow">
				<div style={{ height: 60}}>
				<Title className='titles-g'> Другое </Title>
				<GoButton className='go-button-g' />
				</div>
				</Card>

			</CardGrid>

			
			</div>
			
			


		
	
			
		</Panel>
	);
}

Guides.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Guides;
